// core/rules/engine.ts

export type RuleTrigger = {
  ruleCode: string;
  reason: string;
  legalRef?: string;
  points: number;
  category?: string;
  extra?: any;
};

export type RuleEvaluationResult = {
  triggered: RuleTrigger[];
  deterministicScore: number;
  flags: string[];          // ← new
};

export function evaluateRules(
  tx: any,
  rules: any[]
): RuleEvaluationResult {
  const triggered: RuleTrigger[] = [];
  const flags: string[] = [];
  let deterministicScore = 0;

  for (const r of rules) {
    if (!r.active) continue;

    // Parse JSON payload safely
    const payload =
      typeof r.payload === "string" ? JSON.parse(r.payload) : r.payload || {};

    const severity = Number(r.severity || 0);
    const category = r.category || "GENERAL";

    // -------------------------------
    // THRESHOLD RULES
    // -------------------------------
    if (r.ruleType === "threshold" && payload.field === "amount") {
      const limits = payload.limitByDept || {};
      const deptLimit = tx.department ? limits[tx.department] : undefined;

      if (deptLimit && Number(tx.amount) > Number(deptLimit)) {
        triggered.push({
          ruleCode: r.code,
          reason: `Amount ${tx.amount} exceeds delegated limit ${deptLimit} for dept ${tx.department}`,
          legalRef: payload.legalRef,
          points: severity,
          category,
        });

        flags.push(`LimitExceeded:${tx.department}`);
        deterministicScore += severity;
      }
    }

    // -------------------------------
    // CUSTOM THRESHOLD RULE
    // -------------------------------
    if (r.ruleType === "custom" && payload.threshold != null) {
      if (Number(tx.amount) > Number(payload.threshold)) {
        triggered.push({
          ruleCode: r.code,
          reason: `Amount ${tx.amount} > threshold ${payload.threshold}`,
          legalRef: payload.legalRef,
          points: severity,
          category,
        });

        flags.push("CustomThresholdExceeded");
        deterministicScore += severity;
      }
    }

    // -------------------------------
    // REGEX RULES (support arrays)
    // -------------------------------
    if (r.ruleType === "regex" && payload.field && payload.pattern) {
      const value = String(tx[payload.field] ?? "");

      const patterns = Array.isArray(payload.pattern)
        ? payload.pattern
        : [payload.pattern];

      for (const p of patterns) {
        const patt = new RegExp(p, "i");

        if (patt.test(value)) {
          triggered.push({
            ruleCode: r.code,
            reason: `Field ${payload.field} matches pattern "${p}"`,
            legalRef: payload.legalRef,
            points: severity,
            category,
            extra: { matchedValue: value, pattern: p },
          });

          flags.push(`RegexMatch:${payload.field}`);
          deterministicScore += severity;
          break; // preserve deterministic behavior
        }
      }
    }

    // -------------------------------
    // FUTURE RULE TYPES (ready to plug in)
    // -------------------------------
    // - equality rule
    // - list match rule
    // - supplier blacklist
    // - vendor risk rating rule
    // - geo-location rule
    // - day-of-week rule
    // - anomaly ceiling rule
    // (we leave these empty for now)
  }

  // -------------------------------
  // SCORING CAP
  // -------------------------------
  deterministicScore = Math.min(deterministicScore, 70);

  return { triggered, deterministicScore, flags };
}
