// @core/scoring/riskEngine.ts

import { evaluateRules } from '../../core/rules/engine';
import { runAIChecks } from './aiChecks';

// 🛑 REMOVE the base import:
// import type { PrismaClient } from '@prisma/client/edge'; 

// ✅ Import the specific extended type:
import type { ExtendedPrismaClient } from '@core/prisma/prismaClient';

// Update the function signature to use the correct type alias
export async function runRiskEngine(tx: any, prisma: ExtendedPrismaClient, activeRules: any[]) {
  const { triggered, deterministicScore } = evaluateRules(tx, activeRules || []);
  
  // You may need to do the same for runAIChecks if it also expects the wrong type:
  const ai = await runAIChecks(tx, prisma as any); // Use 'as any' temporarily if runAIChecks source code isn't available

  const aiScore = ai.aiScore;

  const combined = Math.min(Math.round(deterministicScore + aiScore), 100);
  const riskLevel = combined >= 61 ? 'HIGH' : combined >= 31 ? 'WARNING' : 'LOW';
  const status = combined >= 61 ? 'BLOCK' : combined >= 31 ? 'REVIEW' : 'APPROVE';

  const flags = [
    ...triggered.map(t => ({
      rule: t.ruleCode,
      reason: t.reason,
      legalRef: t.legalRef,
      points: t.points
    })),
    ...ai.aiFlags.map((f: any) => ({
      rule: f.name,
      reason: f.message,
      evidence: f.evidence,
      points: f.points
    }))
  ];

  const auditSnapshot = {
    transaction: tx,
    deterministic: { triggered, deterministicScore },
    ai: { flags: ai.aiFlags, aiScore: ai.aiScore },
    combined: { combined, riskLevel, status },
    timestamp: new Date().toISOString()
  };

  return { combined, riskLevel, status, flags, triggered, auditSnapshot };
}
